import React, {Component} from 'react';
import stepData from './stepdata';
import StepCard from './StepCard';

class App extends Component {
   constructor() {
      super();
      this.state = {currentButton : 1, mode : 'learn'};
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
   }
   childClick() {
      this.setState({currentButton : this.state.currentButton + 1, mode : this.state.mode});
   }
   startOver() {
      this.setState({currentButton : 2, mode : this.state.mode});
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   reset() {
      this.setState({currentButton : 1, mode : this.state.mode});
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   upOne(evt) {
      evt.stopPropagation();
      this.setState({currentButton : this.state.currentButton - 1, mode : this.state.mode});
   }
   changeMode() {
      if (this.state.mode === 'learn') {
         this.setState({currentButton : this.state.currentButton, mode : 'test'});
      }
      else this.setState({currentButton : this.state.currentButton, mode : 'learn'});
   }
   render() {
      let stepCards = [];
      if (this.state.mode === 'learn') {
         for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].label} text={stepData[a].text} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} upone={this.upOne} newmode={this.changeMode} />)
      }}
      else {
         for (let a = 0; a < stepData.length; a++) {
         stepCards.push(<StepCard key={stepData[a].id} label={stepData[a].label} text={stepData[a].test} order={stepData[a].order} handler={this.childClick} curbut={this.state.currentButton} upone={this.upOne} newmode={this.changeMode} />)
      }}
      let butStyle;
      if (this.state.currentButton <= stepData.length) butStyle = {pointerEvents : 'none', opacity : 0.5};
      else butStyle = {pointerEvents : 'all', opacity : 1};

      return (
      <div>
      <div id='media-container' className='uk-container uk-float-left uk-width-1-2@m uk-border-rounded'>
      <img src={this.state.currentButton !== 1 ? `sbs_func${String(this.state.currentButton - 1)}.svg` : ''} alt='' style={{height : '100%'}} />
      </div>

      <div id='steps-container' className='uk-float-left uk-width-1-2@m'>
      {stepCards}
      <button className='uk-button uk-button-default uk-button-small uk-width-1-4'>learn</button>
      <button onClick={this.startOver} className='uk-button uk-button-primary uk-button-small uk-width-1-4' style={butStyle}>restart</button>
      <button onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-4'>reset</button>
      <button className='uk-button uk-button-danger uk-button-small uk-width-1-4'>test</button>
      </div>
      </div>
      );
   }
}

export default App;
